@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #041827;
  background-color: #f8fdff;
  min-width: 320px;
}

a {
  color: #000000;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  resize: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  /* stylelint-disable-next-line */
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  text-align: center;
  color: #f2f5f7;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 3px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  background-color: #00b3ff;
}

.modal__wrapper h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 15px;
}

.modal__wrapper p {
  margin: 0;
  margin-bottom: 40px;
  color: #ffffff;
}

.modal__wrapper form {
  display: flex;
  flex-direction: column;
}

.modal__wrapper input {
  border: none;
  border-radius: 3px;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 15px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  color: #ffffff;
  font-size: 16px;
}
.modal__wrapper input::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  opacity: 0.8;
}

.modal__wrapper textarea {
  width: 100%;
  grid-column: 1/3;
  border: none;
  border-radius: 3px;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 25px 15px;
  box-sizing: border-box;
  min-height: 155px;
  margin-bottom: 12px;
}
.modal__wrapper textarea::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  opacity: 0.8;
}

.modal__wrapper button {
  background-color: #041427;
  padding: 15px 60px;
  width: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 700;
  box-sizing: border-box;
  text-align: center;
}

.modal__wrapper label:last-of-type {
  position: relative;
  display: block;
  padding-left: 40px;
  order: 1;
}

.modal__wrapper span:first-of-type {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 40%;
  left: 12px;
  background-color: #00b2ff;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
.modal__wrapper span:last-of-type {
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
  vertical-align: middle;
  display: inline-block;
  margin-top: 15px;
  color: #ffffff;
}

.modal__wrapper input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 3px;
  width: 8px;
  height: 5px;
  background-image: url("../../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.modal__wrapper .modal__close-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  background-image: url("../../img/svg/close-btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  z-index: 1;
}

.modal.is-active {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

